<template>
  <div>
    <apexchart type="line" :series="series" :options="chartOptions"></apexchart>
  </div>
</template>

<script lang="ts">
export default {
  name: "RankingChart",
  props: {
    teams: Array,
    week: Number,
  },
  data: function () {
    var weekNumbers = [...Array(this.week + 1).keys()].slice(1);
    var rankingSeries = [];
    for (let index = 0; index < this.teams.length; index++) {
      const element = this.teams[index];
      rankingSeries.push({
        name: element.teamName,
        data: element.pastRankings,
      });
    }
    return {
      series: rankingSeries,
      chartOptions: {
        chart: {
          height: 350,
          type: "line",
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "straight",
        },
        title: {
          text: "Power Rankings by Week",
          align: "left",
        },
        grid: {
          row: {
            colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5,
          },
        },
        colors: [
          "#F44336",
          "#E91E63",
          "#9C27B0",
          "#6743C1",
          "#6743C1",
          "#0F3F0F",
          "#E0AE38",
          "#D73A44",
          "#F0BCA7",
          "#B1CDC2",
          "#3DF349",
          "#9C592C",
        ],
        xaxis: {
          categories: weekNumbers,
        },
        yaxis: {
          reversed: true,
          labels: {
            show: false,
          },
        },
      },
    };
  },
};
</script>
